import { Avatar, Box, Button, ButtonGroup, Card, CardActions, CardContent, CardHeader, CardMedia, Collapse, IconButton, TextField, Typography } from '@mui/material';
import MoreVertIcon from '@mui/icons-material/MoreVert';
import * as React from 'react';
import { red } from '@mui/material/colors';
import SaveIcon from '@mui/icons-material/Save';
import CancelIcon from '@mui/icons-material/Cancel';
import { useGetFindPeopleByIdQuery, useUpdatePeopleMutation } from '../../store/services/Person'
import { useParams } from 'react-router';
import { useNavigate } from 'react-router-dom'
import { IPerson, Gender } from '../../store/types/Person'
import MainCard from '../../components/cards/MainCard'
import PersonalData from './Forms/PersonalData'
import CardButton from '../../components/cards/CardButton';
import { FetchBaseQueryError } from '@reduxjs/toolkit/dist/query';
import { SerializedError } from '@reduxjs/toolkit';
import { toast } from 'react-toastify';

interface EditProps {
}

const Edit = () => {
    const { id }: any = useParams();
    const navigate = useNavigate();
    const [
        updatePerson,
        { isLoading: isUpdating },
    ] = useUpdatePeopleMutation()
    const { data, error, isLoading } = useGetFindPeopleByIdQuery(id);
    const [personToEdit, setPersonToEdit] = React.useState<IPerson | Partial<IPerson>>({
        id: "",
        firstName: "",
        secondName: "",
        lastName: "",
        nationalId: "",
        birthDate: "2022-06-21T20:14:15.005Z",
        gender: Gender.Personalizado,
        pronounPreference: "Femenino",
        addresses: [],
        mails: [],
        phones: []
    });
    React.useEffect(() => {
        data !== undefined && setPersonToEdit(data);
    }, [data]);
    const saveChanges = () => {
        updatePerson(personToEdit as Partial<IPerson>).then((response: { data: IPerson; } | { error: FetchBaseQueryError | SerializedError; })=>{
            if("data" in response){
                toast.success(`Persona ha sido actualizado existosamente`);
                navigate(`/person`)
            }
            if("error" in response){
                toast.error("Error al momento de crear la persona");
            }
        });
        navigate(`/person`)
    }
    return (data === undefined ? <>Cargando...</> :
        <MainCard
            title="Editar Persona"
            secondary={
                <CardButton type="back" title="Lista de Personas" link="/person" />
            } >
            <Box
                display="flex"
                justifyContent="center"
                alignItems="center"
            >
                <Box display="flex"
                    justifyContent="center"
                    alignItems="center"
                    flexDirection={"column"}
                >
                    <PersonalData person={personToEdit} setPerson={setPersonToEdit} />
                    <br />
                    <ButtonGroup variant="contained">
                        <Button
                            onClick={saveChanges}
                            startIcon={<SaveIcon />}>Guardar todos los cambios</Button>
                    </ButtonGroup>
                </Box>
            </Box>
        </MainCard>);
}

export default Edit;

