export enum Gender {
    Femenino = "Femenino",
    Masculino = "Masculino",
    Personalizado = "Personalizado"
  }
export interface IAddress{
  street:string
  city: number
  state: number
  country: number
  zipCode: string
  latitude: number
  longitude: number
  description: string
}

export interface IMail{
  email:string
  emailDescription: string
}
export interface IPhone{
  phoneNumber:string
  phoneDescription:string
}
export interface IPerson {
    id: string
    firstName: string|undefined
    secondName: string|undefined
    lastName: string|undefined
    birthDate: string
    gender: Gender
    pronounPreference?: string
    nationalId?:string
    addresses: IAddress[]
    mails: IMail[]
    phones:IPhone[]
}